///// BRICKS /////

.bricks-container-header {
	h1 {
		font-size: 52px;
		text-align: center;
		margin-bottom: 2 * $global-margin-bottom;
	}
}

.bricks-container {
	overflow: hidden;
	
	article {
		margin-left: 0;
	}
	.brick {
		width: grid-columns(6);
		min-height: 560px;
		margin-left: 0;
		margin-bottom: 2 * $global-margin-bottom;

		&.education {
			a:hover {
				color: $education-color;
			}
			.cover-image {
				background-color: $education-color;
			}
		}
		&.collection {
			a:hover {
				color: $collection-color;
			}
			.cover-image {
				background-color: $collection-color;
			}
		}
		&.blog {
			a:hover {
				color: $blog-color;
			}
			.cover-image {
				background-color: $blog-color;
			}
		}
		&.exhibition {
			a:hover {
				color: $exhibition-color;
			}
			.cover-image {
				background-color: $exhibition-color;
			}
		}
		&.event {
			a:hover {
				color: $event-color;
			}
			.cover-image {
				background-color: $event-color;
			}
		}

		a:hover {
			.cover-image {
				background-blend-mode: normal;
				filter: grayscale(100%);
			}
		}

		.cover-image {
			background-repeat: no-repeat;
			background-size: cover;
			background-blend-mode: multiply;
			width: 100%;
			height: 312px;
			margin-bottom: $global-margin-bottom;
		}

		h1 {
			font-size: 36px;
			font-weight: normal;
		}
		h3 {
			font-size: 21px;
		}
		p.date {
			font-size: 30px;
			//font-weight: bold;
		}
		.category {
			text-transform: uppercase;
			margin-bottom: 0;

			&.blog {
				color: $blog-color;
			}
			&.education {
				color: $education-color;
			}
			&.collection {
				color: $collection-color;
			}
			&.exhibition {
				color: $exhibition-color;
			}
			&.event {
				color: $event-color;
			}
		}
	}
}
